.profile-options .primary-btn{
    background-color: #ffc107;
    color: white;
    border:2px solid rgb(236, 222, 195);
    text-transform: uppercase;
    font-weight: 900;
}
.profile-options .primary-btn:hover{
    background-color: #ffc107;
color: white;
text-transform: uppercase;
font-weight: 900;
}

/* //////////////////////////////////////////////////////////// */

.profile-options .heighlighted-btn{
    background-color: rgb(54, 54, 54);
    color: white;
    border:2px solid rgb(165, 161, 153);
    margin: 0 0 0 28px;
    text-transform: uppercase;
font-weight: 900;
}
.profile-options .heighlighted-btn:hover{
    background-color: rgb(43, 42, 42);
    color: white;
    margin: 0 0 0 28px;
    text-transform: uppercase;
font-weight: 900;
}

/* /////////////////////////////////////////////////// */

.profile-container{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100vh;
    min-height: fit-content;
    background-image:url(../photos/bacimg.jpg);
    background-repeat: no-repeat;
    border-bottom: 4px solid #ffc107;
}

/* ///////////////////////////////////////////////////// */

.profile-parent{
    display: flex;
    align-items: center;
    color: aliceblue;
    
}

/* ////////////////////////////// */

.profile-parent{
    font-size: 24px;
    font-family:"" ;
}

/* //////////////////////////////////////////// */

.profile-details-role{
    display: flex;
    flex-direction: column;
    flex-direction: column;
    margin: 14px 0 24px 0;
}

.profile-details-role h1{
    font-size: 14px;
    font-family:""; 
}

/* //////////////////////////////////////////////////// */
.primary-text{
color: aliceblue;
}

.heighlited-text{
color: #ffc107;
}

/* //////////////////////////////////////// */

.profile-role-tagline{
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
/* 
/////////////////////////////////////////////////////// */
.profile-picture{
display:flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 1px 0px 0 white;
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0 0 100px;
}

.profile-picture-background{
height: 92%;
width: 92%;
background-image: url(../photos/IMG-20211122-WA0006.jpg);
background-size: cover;
border-radius: 50%;
background-repeat:no-repeat ;
background-position:center ;

}

.profile-picture-background:hover{
    transform: scale(1.07);
    transition: 1s ease-out;
}


/* //////////////////////////////////////////////////// */


.colz .colz-icon  i {
    color: white;
    padding: 18px;
    padding-left: 0px;

}

.colz .colz-icon  i:hover{
transform: scale(1.5);
}

/* 
///////////////////////////////////////////////////////////////////
/////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////// */


@media screen and (max-width:1110px){
    .profile-picture{
        margin: 0 0 0 80px;
        
    }
.profile-details-name{
    font-size: 20px;
}
.profile-details-role .primary-text{
font-size: 20px;
}
.profile-options .btn{
    width: 120px;
    padding: 11px 0;
    z-index: 700;
}
}

/* ////////////////////////////////////////////////////////////////// */

@media screen and (max-width:910px){

.profile-picture{
    height: 320px;
    width: 320px;
    margin: 0 0 0 4px;
}

}

/* 
//////////////////////////////////////////////// */

@media screen and (max-width:810px){

    .profile-picture{
        height: 320px;
        width: 320px;
        margin: 0 !important;
    }
    .profile-parent{
        flex-direction: column-reverse;
        margin: 40xp 0 0 0;
    }
.profile-details{
    margin: 25px 0 0 ;
}
.profile-details-role{
    text-align: center;
}
    }
    
    /* //////////////////////////////////////////////////////////// */

    @media screen and (max-width:380px){

.profile-picture{
    height: 275px;
    width: 275px;
    margin-top: -90px;
}
.profile-details{
    width: 90%;
}
    }

    @media screen and (max-width:280px){

        .profile-picture{
            height: 175px;
            width: 175px;
            
        }
        .profile-details{
            width: 90%;
        }
    }