.servics{
  
    overflow:hidden;
margin-bottom: 1rem;
padding-bottom: 1rem;
}
.section-heading{
    color: black;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
    line-break: 0.5;
    margin-top: 2rem;
}
.section-heading span{
    color:#ffc107 ;
}

.card{
    overflow: hidden;
    width: 18rem;
    margin: auto;
    box-shadow: 0 4px 8px black;
    margin-top: 10px;
}

.card-wrapper{
    display: flex;
    position: relative;
    flex-wrap: wrap;
}

.card img:hover{
    transform: scale(1.07);
    transition: 1s ease-out;
}