
.header{
    text-transform: uppercase;
    font-weight: 900;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    position: fixed;
    display: flex;
    z-index: 100;
height: 10vh;
    width: 100vw;
    background-color: black;
    border: 2px solid rgb(31, 30, 30);
}
/* 
////////////////////////////////// */
.logo{
    background-image:url('../photos/logo.png') ;
    height: 50px;
    width: 50px;
  background-repeat:no-repeat ;
position: absolute;
display: none;
top: 16px;
right: 16px;
}
.navbar{
    margin: auto;
    margin-right: 16px;
padding: 16px 32px;
  display: none;
   margin-right: 4px;
  
}

.navBtn{
    color: white;
margin: 1rem;
    text-decoration: none;
 
 padding: 0 60px;
}

.navBtn:hover{
    background-color:rgb(255, 238, 0) ;
    color: black;
    font-weight: 400;
    border: 2px solid white;
    border-radius: 18px;
}



/* //////////////////////////////////////////////////////////////////// */

.menustar{
display: block;
position: relative;
z-index: 1;
user-select: none;
appearance: none;
border: none;
outline: none;
background: none;
cursor: pointer;
}

.menustar span{
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background-color: white;
    border-radius: 6px;
    z-index: 1;
    transform-origin:0 0;
    transition: 0.4s;
}

.menustar:hover span:nth-child(2){
transform: translateX(10px);
background-color: yellow;
}

.menustar.is-active span:nth-child(1){
    transform: translate(0px ,-2px) rotate(45deg);

}
.menustar.is-active span:nth-child(3){
    transform: translate(-3px ,3px) rotate(-45deg);

}
.menustar.is-active span:nth-child(2){
    opacity: 0;

}

.menustar.is-active:hover span{
    background-color: yellow;
}

@media (min-width:1024px){
    .menustar{
        display: none;

    }
    .navbar{
        display: block;
        
    }
  
    .logo{
        display: block;
    }
}

 .header.is-active{
    width: 100vw;

    height: 70%;
    background-color: rgba(0, 0, 0, 0.801);
}
.navbar.is-active{
    align-items: center;
    padding: auto ;
    display: flex;
    padding-left: 22%;
       
}

.navbar.is-active .navBtn{
    color: white;
margin: 9%;
    text-decoration: none;
 align-items: center;
 justify-content: center;
 text-align: center;
 padding: auto;
}

.navbar.is-active .navBtn:hover{
    color: black;
}



.header.is-active .menustar{
    position: absolute;
    top: 16px;
    bottom: 16px;
    font-weight: bolder;
}

@media  (min-width:(500px)) {
    
    .navbar.is-active .navBtn{
        color: white;
        display: inline-block;
    margin: auto;
       
     padding: auto;
    }

}



@media (max-width:1024px){
    .header{
       height: 5vh;
    }
}